.o-img-animate-2, .o-img-animate-5{

    div{
        position: relative; 
    }
    

    img{
        width: 100%;
    }
}


@keyframes rounda-1 {
    0% {
        opacity:1;
    }
    50%{
        opacity: 1;
    }
    51%{
        opacity: 0;
    }
    100%{
        opacity: 0;
    }
}

@keyframes rounda-2 {
    0% {
        opacity:0;
    }
    50%{
        opacity: 0;
    }
    51%{
        opacity: 1;
    }
    100%{
        opacity: 1;
    }
}





