.o-img-animate-2 div img, .o-img-animate-5 div img{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-duration: 2s;
    height: 100%;
}

.o-img-animate-2 div img:first-child, .o-img-animate-5 div img:first-child{
    position: relative;
    opacity: 1;
}

.o-img-animate-2 div img:nth-child(1){animation-name: rounda-1;}
.o-img-animate-2 div img:nth-child(2){animation-name: rounda-2;}


.o-img-animate-5 div img:nth-child(1){animation-name: roundb-1;}
.o-img-animate-5 div img:nth-child(2){animation-name: roundb-2;}
.o-img-animate-5 div img:nth-child(3){animation-name: roundb-3;}
.o-img-animate-5 div img:nth-child(4){animation-name: roundb-4;}
.o-img-animate-5 div img:nth-child(5){animation-name: roundb-5;}