@import url(/css/round.scss);

$border: 2px solid #000;
$color: #CDCDCD;

html, body{
    margin: 0;
    width: 100%;
    background-color: $color;
}

@font-face {
    font-family: afont;
    src: url("/assets/afont.otf");
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, p, a, table, td, input, label, button, span, li{
    font-family: afont, sans-serif;
    font-weight: normal;
}

p{
    line-height: 180%;
}

a{
    text-decoration: none;
    color: #000;
}

label, input{
    display: block;

    &:focus{
        outline: none;
    }
}

select, input, button{
    background-color: transparent;
    border: none;
    cursor: pointer;
}

ul{
    margin: 0;
    padding: 0;
    list-style: none;
}







//NAV

nav{
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    z-index: 10;
    width: 100%;
    top: 0;
    left: 0;
    padding: 30px 5%;

    & > .logo{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -40%);
        display: none;
    }

    .c-nav-cart{
        img{
            width: 20px;
        }

        a:first-child{
            margin-right: 30px;
        }
    }

    .c-nav-info-btn__about{
        display: flex;
        align-items: center;

        .c-link-btn{
            border: none;
            padding: 0;
            margin-right: 20px;
            font-size: 20px;
            margin-top: -4px;
        }

        img{
            width: 20px;
        }
    }

    .c-nav-info-btn__search{
        display: none;
        align-items: center;

        button{
            img{
                width: 15px;
            }
        }

        input{
            border-left: 3px solid #000;
            padding: 2px 0 2px 8px;
            margin-left: 8px;
            font-size: 16px;
        }
    }
}


.o-mob-logo{
    display: block;
}


.js-search{
    .c-nav-info-btn__about{
        display: none;
    }

    .c-nav-info-btn__search{
        display: flex;
    }
}




//LOADER

.c-loader{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 15;
    background-color: $color;
    padding-top: 12%;

    & > img{
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translate(-50%, 0);
        width: 160px;
    }

    & > div:last-child{
        position: absolute;
        bottom: 10%;
        left: 50%;
        transform: translate(-50%, 0);
        width: 60%;
    }
}


//MAIN

main{
    padding: 20% 6vw;
}

.o-popup{
    position: fixed;
    z-index: 10;
}



//ABOUT

.c-home{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    padding: 0 5%;
    position: absolute;
    bottom: 10%;
    left: 0;

    & > div{
        width: 50%;
    }
}

.c-home-logo{
    display: block;
    text-align: center;
    margin-top: 30%;

    img{
        width: 180px;
    }
}




//PRODUCT-PAGE


.o-product-flex{
    display: block;
}

.c-product-img{
    width: 100%;

    a{
        text-align: center;
        margin: 30px 0;

        img{
            width: 130px;
            text-align: center;
        }
    }

    div{
        width: 55%;
        margin: auto; 
    }
    
}

.c-product-info{
    width: 100%;
}

.c-product-title{
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin: 40px 0 25px 0;

    h2{
        font-size: 18px;
    }

    h2:first-child{
        width: 75%;
    }
}

.c-product-para{
    margin-bottom: 30vh;
    p{
        font-size: 10px;
    }
}

.c-product-desc{
    margin-bottom: 20px;
}

.c-product-cta{
    position: fixed;
    bottom: 10%;
    width: 100%;
    left: 0;
    background-color: $color;
    padding: 0 6vw;

    .c-size-selector{
        width: 100%;
        display: flex;
        justify-content: space-between;

        .active-size{
            border: 4px solid #000;
        }

        button{
            width: 50px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            background-color: transparent;
            border-radius: 100px;
            border: 2px solid rgba($color: #000000, $alpha: .5);
        }
    }

    & > button{
        width: 100%;
        background-color: #000;
        color: #fff;
        text-align: center;
        padding: 25px 0;
        border: none;
        margin-top: 25px;
        border-radius: 200px;
    }
}



//PRODUCT SIZE


.c-product-size{
    display: none;
    top: 5vh;
    left: 5%;
    padding: 0 6%;
    border: $border;
    background-color: #fff;
    width: 90%;
    border-radius: 2rem;
    overflow-y: auto;
    height: 90vh;

    .active-unit, .active-size{
        color: #fff;
        background-color: #000;
    }

    .c-product-size-btns{
        ul{
            display: flex;
            margin-top: 10px;

            li{
                border: 1px solid #000;
                border-radius: 50px;
                line-height: 40px;
                text-align: center;
                margin-right: 8px;
                cursor: pointer;
            }

            .active{
                color: #fff;
                background-color: #000;
            }
        }

        ul:nth-child(2) li{
            width: 40px;
            height: 40px;
        }

        ul:nth-child(3) li{
            padding: 0 30px;
        }
    }

    p, li{
        font-size: 10px;
        padding-bottom: .8rem;
    }

    .o-size-img{
        width: 100%;
    }

    & > div > div:first-child{
        padding: 40px 0 30px 0;
        position: relative;
        height: max-content;

        .o-close{
            position: absolute;
            top: 15px;
            left: 0px;
            z-index: 4;

            img{
                width: 15px;
            }
        }

        ul{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            & > li{
                position: absolute;
                background-color: #fff;
                padding: 0 6px;
            }
        }
    }

    #js-shoulder{
        top: 16%;
        left: 41%;
    }

    #js-chest{
        top: 62%;
        left: 42%;
    }

    #js-arm{
        top: 40%;
        left: 12%;
    }

    #js-torso{
        right: -4%;
        bottom: 30%;
    }

    & > div > div:last-child{
        hr{
            border: 1px solid #afafaf;
            margin-bottom: .8rem;
        }

        div{
            margin-bottom: 26px; 
        }     
    }
}

.c-product-nav-btns{
    display: flex;
    align-items: center;
    padding-top: 25px;
    position: fixed;
    left: 6%;
    bottom: 2%;

    a{
        border: $border;
        padding: 12px 24px;
        display: flex;
        font-size: 8px;
        align-items: center;
        border-radius: 100px
    }

    a:first-child{
        margin-right: 20px;
        img{
            margin: 0 17px 0 0;
        }
    }

    a:last-child{
        margin-right: 35px;
        img{
            transform: rotate(-180deg);
            margin: 0 0 0 17px;
        }
    }
}



//ACCOUNT LOGIN SIGNUP


.c-account{
    display: none;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: fixed;

    .o-close{
        position: absolute;
        width: 50%;
        height: 5px;
        border-radius: 20px;
        background-color: #000;
        left: 25%;
        top: 20px;
        z-index: 3;
    }

    form{
        width: 100%;
        & > button{
            width: 100%;
            padding: 25px 0;
            font-size: 16px;
            border: none;
            background-color: #000;
            color: #fff;
        }

        label{
            padding-bottom: 10px;
        }

        input{
            width: 100%;
            border: none;
            background-color: transparent;
            font-size: 24px;

            &:focus{
                border: none;
            }
        }
    }

    & > div{
        background-color: $color;
        width: 100%;
        height: 100%;
        position: relative;

        img{
            width: 120px;
            margin: auto;
            display: block;
            margin-bottom: 50px;
        }

        & > div{
            padding: 0 6%;
            width: 100%;
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);

            & > a{
                font-size: 18px;
                text-align: center;
                display: block;
                margin-top: 20px;
            }
            
            .o-input-field{
                position: relative;
                margin-bottom: 25px;

                &::after, &::before{
                    content: "";
                    position: absolute;
                    bottom: 0;
                    width: 100%;
                    height: 2px;
                    left: 0;
                }

                &::before{
                    background-color: #000;
                    opacity: .4;
                }

                &::after{
                    background-color: #000;
                    transition: .4s;
                    width: 0;
                }

                &:focus-within::after{
                    width: 100%;
                }
            }
        }
    }
    
}

.c-login{
    display: none;
}




//CART/BAG

.c-bag{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none; //temp hidden

    .o-close{
        position: absolute;
        color: #000;
        right: 5%;
        top: 30px;
        z-index: 3;
        cursor: pointer;
    }

    & > div{
        width: 100%;
        height: 100%;
        background-color: $color;
        position: relative;
        padding: 10% 5%;

        & > img{
            display: block;
            width: 90px;
            position: absolute;
            top: 36px;
            left: 5%;
        }
    }

    .c-bag-item-wrap{
        height: 63%;
        margin-top: 15%;
        overflow-y: auto;
    }
}

.c-bag-item{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin: 20px 0;

    h3, option{
        font-size: 12px;
    }

    select{
        -moz-appearance:none;
        -webkit-appearance:none;
        appearance:none;
        background: url(/assets/dropdown.svg);
        background-repeat: no-repeat;
        padding-right: 15px;
        background-position: 100% 50%;
        margin-top: 5px;
    }

    .c-bag-img{
        width: 28%;
    }

    .c-bag-item-desc{
        width: 72%;
        margin-left: 25px;

        & > div:first-child{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 5px;
        }

        & > div:last-child{
            & > div:first-child{
                display: flex;
                align-items: center;

                p{
                    margin: 0 5px;
                }
            }
        }
    }
}

.c-bag-cta{
    position: absolute;
    bottom: 30px;
    width: 100%;
    left: 0;
    padding: 0 5%;

    input, h3{
        font-size: 20px;
        margin: 5px 0;
    }

    div{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    button{
        width: 100%;
        color: #fff;
        background: #000;
        padding: 35px 0;
        margin-top: 30px;
        font-size: 16px;
    }
}



//ABOUT

.c-about{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none;

    .o-about-close-btn{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    & > div:nth-child(2){
        position: absolute;
        top: 10%;
        left: 5%;

        
        & > div:nth-child(2){
            background-image: url(/assets/info.png);
            background-size: 100%;
            background-position: center;
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            border: $border;
            width: 320px;
            margin: 20px 0;

            img{
                width: 130px;
                filter: invert(1);
                margin-top: 4px;
            }
        }

        & > div:nth-child(3){
            background: url(/assets/abt2.png);
            background-size: cover;
            background-position: center;
            border: $border;
            width: 290px;
            padding: 20px;
            color: #FFF;

            img{
                width: 130px;
                filter: invert(1);
            }

            p{
                font-size: 12px;
                margin: 35px 0 50px 0;
                text-align: center;
            }
        }
    }
    

    .c-about-links{
        background-color: #F6F7EF;
        padding: 25px;
        border: $border;
        width: 210px;
        a{
            display: block;
            margin: 12px 0;
            font-size: 14px;
        }
    }

    .c-about-sub{
        background: url(/assets/sub.png);
        background-size: 100%;
        background-position: center;
        border: $border;
        width: 300px;
        padding: 25px;
        color: #fff;
        margin-top: 20px;
        
        form{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        input, button{
            color: #fff;
        }
    }
}



//CATALOGUE

.c-catalogue-section{
    margin: 40px 0;
    h3{
        margin-bottom: 10px;
    }
}



//DETAILS

.c-about-details{
    position: absolute;
    top: 10%;
    width: 100%;
    height: 90vh;
    pointer-events: none;

    & > div{
        position: absolute;
        top: 0;
        left: 5%;
        width: 90%;
        padding: 50px 5%;
        border: $border;
        display: none;
        max-height: 80%;
        overflow-y: auto;
        pointer-events: all;
        background-color: #F6F7EF;

        h3{
            padding-top: 40px;
        }

        p{
            padding-bottom: 50px;
        }
    }

    #js-info{
        img{
            width: 200px;
            display: block;
            margin: 50px auto;
            filter: invert(1);
        }
        color: #fff;
        text-align: center;
        font-size: 12px;
        background: url(/assets/info.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-attachment: fixed;
    }
}